import { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import { format } from "date-fns";
import numeral from "numeral";
import PropTypes from "prop-types";
import {
  Box,
  Button,
  Card,
  Checkbox,
  InputAdornment,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  Typography,
} from "@material-ui/core";

import SearchIcon from "../../../icons/Search";
import Label from "../../../components/Label";
import Scrollbar from "../../../components/Scrollbar";
import Dialog from "../feereport/FeeReport";
const statusOptions = [
  {
    label: "All",
    value: "all",
  },
  {
    label: "Paid",
    value: "paid",
  },
  {
    label: "unpaid",
    value: "unpaid",
  },
  {
    label: "Pending",
    value: "pending",
  },
 
];

const sortOptions = [
  {
    label: "Newest first",
    value: "createdAt|desc",
  },
  {
    label: "Oldest first",
    value: "createdAt|asc",
  },
];

const getStatusLabel = (invoiceStatus) => {
  const map = {
    unpaid: {
      color: "warning",
      text: "unpaid",
    },
    paid: {
      color: "success",
      text: "Paid",
    },
    pending: {
      color: "error",
      text: "Pending",
    },
  };

  const { text, color } = map[invoiceStatus];

  return <Label color={color}>{text}</Label>;
};

const applyFilters = (invoices, query, filters) =>
  invoices.filter((invoice) => {
    // console.log("invoice", invoice);

    let matches = true;

    if (query) {
      const properties = ["name", "email"];
      let containsQuery = false;

      properties.forEach((property) => {
        if (invoice[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    if (filters.status && invoice.status !== filters.status) {
      matches = false;
    }

    return matches;
  });

const applyPagination = (invoices, page, limit) =>
  invoices.slice(page * limit, page * limit + limit);

const InvoiceListTable = (props) => {
  const { invoices, ...other } = props;
//   console.log("invoices", invoices);
  const [selectedInvoices, setSelectedInvoices] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    status: null,
  });

  const handleQueryChange = (event) => {
    setQuery(event.target.value);
  };

  const handleStatusChange = (event) => {
    console.log(event.target.value);
    let value = null;

    if (event.target.value !== "all") {
      value = event.target.value;
    }

    setFilters((prevFilters) => ({
      ...prevFilters,
      status: value,
    }));
    // console.log("value", filters);
  };

  const handleSortChange = (event) => {
    setSort(event.target.value);
  };

  const handleSelectAllInvoices = (event) => {
    // console.log("1",event.target.checked);
    setSelectedInvoices(
      event.target.checked ? invoices.map((invoice) => invoice.id) : []
    );
  };

  const handleSelectOneInvoice = (event, invoiceId) => {
    // console.log("2", invoiceId);
    if (!selectedInvoices.includes(invoiceId)) {
      setSelectedInvoices((prevSelected) => [...prevSelected, invoiceId]);
    } else {
      setSelectedInvoices((prevSelected) =>
        prevSelected.filter((id) => id !== invoiceId)
      );
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value, 10));
  };

  // Usually query is done on backend with indexing solutions
  const filteredInvoices = applyFilters(invoices, query, filters);
  const paginatedInvoices = applyPagination(filteredInvoices, page, limit);
  const selectedSomeInvoices =
    selectedInvoices.length > 0 && selectedInvoices.length < invoices.length;
  const selectedAllInvoices = selectedInvoices.length === invoices.length;

  return (
    <Card {...other}>
      <Box
        sx={{
          alignItems: "center",
          display: "flex",
          flexWrap: "wrap",
          m: -1,
          p: 2,
        }}
      >
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 500,
          }}
        >
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="end">
                  <SearchIcon fontSize="small" />
                </InputAdornment>
              ),
            }}
            onChange={handleQueryChange}
            placeholder="Search invoices by Student Name"
            value={query}
            variant="outlined"
          />
        </Box>
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 240,
          }}
        >
          <TextField
            fullWidth
            label="Sort By"
            name="sort"
            onChange={handleSortChange}
            select
            SelectProps={{ native: true }}
            value={sort}
            variant="outlined"
          >
            {sortOptions.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </TextField>
        </Box>
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 240,
          }}
        >
          <TextField
            fullWidth
            label="Status"
            name="status"
            onChange={handleStatusChange}
            select
            SelectProps={{ native: true }}
            value={filters.status || "all"}
            variant="outlined"
          >
            {statusOptions.map((statusOption) => (
              <option key={statusOption.value} value={statusOption.value}>
                {statusOption.label}
              </option>
            ))}
          </TextField>
        </Box>
      </Box>
      
      <Scrollbar>
        <Box sx={{ minWidth: 1200 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllInvoices}
                    color="primary"
                    indeterminate={selectedSomeInvoices}
                    onChange={handleSelectAllInvoices}
                  />
                </TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Roll No</TableCell>
                <TableCell>Status</TableCell>
                <TableCell>Amount</TableCell>
                <TableCell>Class</TableCell>
                <TableCell>Section</TableCell>

                <TableCell align="start">Phone</TableCell>

                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedInvoices.map((invoice) => {
                const isInvoiceSelected = selectedInvoices.includes(invoice.id);
                console.log("inv", invoice);

                return (
                  <TableRow hover key={invoice.id} selected={isInvoiceSelected}>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isInvoiceSelected}
                        color="primary"
                        onChange={(event) =>
                          handleSelectOneInvoice(event, invoice.id)
                        }
                        value={isInvoiceSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Link
                        color="textPrimary"
                        component={RouterLink}
                        to="#"
                        underline="none"
                        variant="subtitle2"
                      >
                        {invoice.name}
                      </Link>
                      <Typography color="textSecondary" variant="body2">
                        {invoice.email}
                      </Typography>
                    </TableCell>
                    <TableCell>{invoice.rollno}</TableCell>
                    {/* <TableCell>{getStatusLabel(invoice.status)}</TableCell> */}
                    <TableCell>
                      {invoice.status == 1 ? "Active" : "Non-Active"}
                    </TableCell>

                    <TableCell>{invoice.Balance}</TableCell>
                    <TableCell>{invoice.class}</TableCell>
                    <TableCell>{invoice.section}</TableCell>

                    <TableCell>{invoice.phone}</TableCell>

                    
                    <TableCell>
                      <Dialog
                        text="Fee Report"
                        invoiceData={invoice}
                        rollno={invoice.rollno}
                      />
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </Scrollbar>
      <TablePagination
        component="div"
        count={filteredInvoices.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

InvoiceListTable.propTypes = {
  invoices: PropTypes.array.isRequired,
};

export default InvoiceListTable;
