import { useState, useEffect, useCallback } from "react";
import { Link as RouterLink } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import { Box, Container } from "@material-ui/core";
import { invoiceApi } from "../../__fakeApi__/invoiceApi";
import InvoiceListTable from "./invoice/InvoiceListTable";
import useMounted from "../../hooks/useMounted";
import useSettings from "../../hooks/useSettings";
import gtm from "../../lib/gtm";
import Design from "../Design";
import axios from "axios";
import {customAxios} from "../apis/Axios";

const FeeDetail = () => {
  const mounted = useMounted();
  const { settings } = useSettings();
  const [feeDetails, setFeeDetails] = useState([]);

  const studentData = async () => {
    const data = await customAxios.getCall("list/list");
    setFeeDetails(data);
    // console.log("iiii",data.data)
  };

  useEffect(() => {
    gtm.push({ event: "page_view" });
    studentData();
  }, []);

  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Design text="Fee Transection Detail" dashText="Fee Detail" />
          <Box sx={{ mt: 3 }}>
            <InvoiceListTable invoices={feeDetails} />
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default FeeDetail;
