import React, { useRef, useState } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import moment from "moment/moment";
import {customAxios} from "../../apis/Axios";
  const useStyles = makeStyles({
    table: {
      minWidth: 700,
    },
  });
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

export default function FeeReport({ text, rollno, invoiceData }) {
  
  const [open, setOpen] = useState(false);
  const [feeDetails, setFeeDetails] = useState([]);
  const classes = useStyles();
  const handleClickOpen = async () => {
    setOpen(true);
    // alert(rollno);
    const Responce = await customAxios.getCall(`fee/single/${rollno}`);
    setFeeDetails(Responce);
  };
  const TAX_RATE = invoiceData.Balance;

  function ccyFormat(num) {
    return `${num.toFixed(1)}`;
  }

  function priceRow(paid) {
    return paid;
  }

  function subtotal(items) {
    return items.reduce((sum, i) => sum + i, 0);
  }

  // const datas = feeDetails.map((ele,index) => {
  //   console.log(ele.paid,index);
  //   return ele.paid;
  // });

  var invoiceSubtotal = subtotal(feeDetails.map((ele) => ele.paid));
  const invoiceTaxes = invoiceSubtotal;
  const invoiceTotal = TAX_RATE - invoiceTaxes;

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button
        variant="contained"
        size="small"
        color="primary"
        onClick={handleClickOpen}
      >
        {text}
      </Button>
      <Dialog
        fullScreen
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{"Fee Report"}</DialogTitle>
        <DialogContent style={{ width: "100%" }}>
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="spanning table">
              <TableHead>
                <TableRow style={{ border: " 1px solid black" }}>
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="start"
                    colSpan={3}
                  >
                    <strong>Details</strong>
                  </TableCell>
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="right"
                  >
                    Price
                  </TableCell>
                </TableRow>
                <TableRow style={{ border: " 1px solid black" }}>
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="start"
                    colSpan={1}
                  >
                    <strong>{` Name : ${invoiceData.name} `}</strong>
                  </TableCell>

                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="start"
                  >
                    <strong>{`Father's Name : ${invoiceData.fname} `}</strong>
                  </TableCell>
                </TableRow>
                <TableRow style={{ border: " 1px solid black" }}>
                  <TableCell style={{ border: " 1px solid black" }}>
                    Date
                  </TableCell>
                  <TableCell
                    colSpan={2}
                    style={{ border: " 1px solid black" }}
                    align="start"
                  >
                    Paid
                  </TableCell>

                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="right"
                  >
                    Sum
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {feeDetails.map((row) => (
                  <TableRow  key={row.id}>
                    <TableCell style={{ border: " 1px solid black" }}>
                      {moment(row.date).utc().format("YYYY-MMM-DD")}
                    </TableCell>
                    <TableCell
                      colSpan={2}
                      style={{ border: " 1px solid black" }}
                      align="start"
                    >
                      {row.paid}
                    </TableCell>

                    <TableCell
                      style={{ border: " 1px solid black" }}
                      align="right"
                    >
                      {ccyFormat(row.paid)}
                    </TableCell>
                  </TableRow>
                ))}

                <TableRow>
                  <TableCell
                    style={{ borderRight: " 1px solid black" }}
                    rowSpan={3}
                  />
                  <TableCell style={{ border: " 1px solid black" }} colSpan={2}>
                    <b>Total Fee</b>
                  </TableCell>
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="right"
                  >
                    <b>{ccyFormat(TAX_RATE)}</b>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell
                    color="primary"
                    style={{ border: " 1px solid black" }}
                    colSpan={2}
                  >
                    <b> Total Paid</b>
                  </TableCell>
                  {/* <TableCell align="right">{`${TAX_RATE.toFixed(
                    0
                  )} `}</TableCell> */}
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="right"
                  >
                    <b>{ccyFormat(invoiceTaxes)}</b>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell style={{ border: " 1px solid black" }} colSpan={2}>
                    <b>Status</b>
                  </TableCell>
                  <TableCell
                    style={{ border: " 1px solid black" }}
                    align="right"
                  >
                    <b>
                      {" "}
                      {ccyFormat(invoiceTotal) != 0
                        ? ccyFormat(invoiceTotal)
                        : "Paid"}
                    </b>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </DialogContent>
        <DialogActions>
         
          <Button onClick={handleClose} color="success" variant="contained">
            Save
          </Button>
        </DialogActions>
      </Dialog>
      
    </div>
  );
}
